import pick from 'lodash.pick'

import { Serializable } from './serializable.interface';
import { Deserializable } from './deserializable.interface';

export class AuthToken implements Serializable, Deserializable {
    public accessToken: string;
    public expireAt: Date;
    public account: {
        id: number,
        email: string,
        firstName: string,
        lastName:  string,
        isActive: boolean,
        isAdmin: boolean
    };

    serialize(): string {
        const keys = [
            `accessToken`,
            `expireAt`,
            `account`
        ];

        const obj: Object = {
            ...pick(this, keys),
            ...( !!this.expireAt ? { expireAt: this.expireAt.toISOString() } : {} )
        };

        return JSON.stringify(obj);
    }

    deserialize(input: any): this {
        const props = {
            ...input,
            ...input.expireAt
                ? { expireAt: new Date(input.expireAt) }
                : {}
        }
        return Object.assign(this, props); // TODO: test this
    }

    isExpired(): boolean {
        return new Date() > this.expireAt
    }
}
