import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { AuthService } from 'src/app/services/auth/auth.service';
import { AuthToken } from 'src/app/models/auth-token.model';
import { map, catchError } from 'rxjs/operators';
import { Observable, BehaviorSubject, of } from 'rxjs';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;

  loggedIn$: Observable<boolean>;
  loginErrorBehaviorSubject: BehaviorSubject<string|null> = new BehaviorSubject<string|null>(null);
  loginError$: Observable<string | null>;

  constructor(private formBuilder: FormBuilder, private authService: AuthService, private router: Router) { }

  ngOnInit() {
    this.loggedIn$ = this.authService.loggedIn$;
    this.loginError$ = this.loginErrorBehaviorSubject.asObservable();

    this.loginForm = this.formBuilder.group({
      login: new FormControl('', [Validators.required]),
      password: new FormControl('', [Validators.required])
    });
  }

  onLoginFormSubmit(form: FormGroup): void {
    if (form.invalid) return;

    this.authService.login(form.value.login, form.value.password)
      .subscribe(
        (authToken: AuthToken) => {
          this.loginErrorBehaviorSubject.next(null);
          this.router.navigate(['deployments']);
        },
        (error: string) => {
          this.loginErrorBehaviorSubject.next(error);
          return of(undefined);
        }
      )
  }
}
