import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable, of, throwError, BehaviorSubject } from 'rxjs';
import { map, catchError, tap, publishReplay, shareReplay, finalize, share } from 'rxjs/operators'

import { AuthToken } from 'src/app/models/auth-token.model';

import { LoginRequest } from 'src/app/models/api/auth/login/login-request';
import { LoginResponseSuccess } from 'src/app/models/api/auth/login/login-response-success.interface';
import { LoginResponseFailed } from 'src/app/models/api/auth/login/login-response-failed.interface';

import { LogoutResponseSuccess } from 'src/app/models/api/auth/logout/logout-response-success.interface';
import { LogoutResponseFailed } from 'src/app/models/api/auth/logout/logout-response-failed.interface';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  private currentAuthTokenBehaviorSubject: BehaviorSubject<AuthToken|null>
  public currentAuthToken$: Observable<AuthToken|null>;
  public loggedIn$: Observable<boolean>;

  private lastLoginPendingRequest$: Observable<AuthToken> | null;
  private loginPendingBehaviorSubject: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  public loginPending$: Observable<boolean>;

  constructor(private httpClient: HttpClient, private router: Router) {
    const existingAuthToken = environment.POLICY_RESTORE_AUTH_THROUGH_LOCALSTORAGE ? this.getAuthTokenFromLocalStorage() : null;
    this.currentAuthTokenBehaviorSubject = new BehaviorSubject<AuthToken|null>(existingAuthToken);
    this.currentAuthToken$ = this.currentAuthTokenBehaviorSubject.asObservable();
    this.loggedIn$ = this.currentAuthToken$.pipe(
      map((authToken: AuthToken | null) => this.authTokenValid(authToken)),
      shareReplay()
    )
    this.loginPending$ = this.loginPendingBehaviorSubject.asObservable();
  }

  get currentAuthToken(): AuthToken | null {
    return this.currentAuthTokenBehaviorSubject.value;
  }

  get isLoginPending(): boolean {
    return this.loginPendingBehaviorSubject.value;
  }

  get isLoggedIn(): boolean {
    return this.authTokenValid(this.currentAuthToken)
  }

  login(login: string, password: string): Observable<AuthToken> {
    if (this.isLoggedIn) return of(this.currentAuthToken);
    if (this.isLoginPending) return this.lastLoginPendingRequest$;

    this.loginPendingBehaviorSubject.next(true)

    this.lastLoginPendingRequest$ = this.attemptLogin(login, password).pipe(
      map((authToken: AuthToken) => {
        this.currentAuthTokenBehaviorSubject.next(authToken);
        if (environment.POLICY_RESTORE_AUTH_THROUGH_LOCALSTORAGE) this.storeAuthTokenInLocalStorage(authToken);
        return authToken;
      }),
      catchError((error: string) => throwError(error)),
      shareReplay(),
      finalize(() => this.loginPendingBehaviorSubject.next(false)),
    );

    return this.lastLoginPendingRequest$;
  }

  logout(): Observable<boolean> {
    if (!this.isLoggedIn) return of(false);
    this.clearAuthTokenInLocalStorage();
    this.currentAuthTokenBehaviorSubject.next(null);
    this.router.navigate(['login']);
    if (!this.isLoggedIn) return of(true);
    // TODO: Remove below if is not used?
    // return this.httpClient.delete<LogoutResponseSuccess>(environment.API_AUTH_ENDPOINT).pipe(
    //   map(() => {
    //     this.currentAuthTokenBehaviorSubject.next(null);
    //     return true;
    //   }),
    //   catchError((data: LogoutResponseFailed) => throwError(data.error)),
    //   shareReplay()
    // );
  }

  private authTokenValid(authToken: AuthToken | null): boolean {
    return !!authToken && !authToken.isExpired();
  }

  private attemptLogin(login: string, password: string): Observable<AuthToken> {
    return this.httpClient.post<LoginResponseSuccess|LoginResponseFailed>(environment.API_AUTH_ENDPOINT, <LoginRequest>{ login: login, password: password }).pipe(
      map((data: LoginResponseSuccess) => new AuthToken().deserialize(data)),
      catchError((data: { error: LoginResponseFailed }) => throwError(data.error.errors.base.join(`, `)))
    );
  }

  private storeAuthTokenInLocalStorage(authToken: AuthToken): void {
    localStorage.setItem(environment.AUTHTOKEN_LOCALSTORAGE_KEY, authToken.serialize());
  }

  private getAuthTokenFromLocalStorage(): AuthToken | null {
    const str = localStorage.getItem(environment.AUTHTOKEN_LOCALSTORAGE_KEY);

    try {
      const data = JSON.parse(str);
      const authToken = new AuthToken().deserialize(data);
      if (!authToken) return null;
      if (authToken.isExpired()) return null;
      return authToken;
    } catch {
      return null;
    }    
  }

  private clearAuthTokenInLocalStorage(): void {
    localStorage.removeItem(environment.AUTHTOKEN_LOCALSTORAGE_KEY);
  }
}
